<template>
    <transition name="modal" >
        <div class="my-popups">
            <div class="modal d-block">
               <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                       <div class="modal-header">
                          <h5 class="modal-title">  {{$t('FollowUp')}}</h5>
                            <button v-on:click="closePopup()" type="button" class="close"><em class="fa fa-times" aria-hidden="true"></em></button>
                        </div>
                        <div class="modal-body" style="max-height:600px;overflow-y:auto;">
                            <div class="listing">
                              <div class="table-responsive" v-if="followupList.length > 0">
                                    <table class="table table-bordered dt-responsive nowrap mt-0" id="table-openactivities-content" aria-describedby="folowuplist">
                                        <thead class="thead-bg">
                                            <tr>
                                                 <th scope="col"><span>  {{$t('Subject')}} </span></th>
                                                 <th scope="col"><span> {{$t('Activity')}}  {{$t('Type')}}  </span></th>
                                                 <th scope="col"><span> {{$t('Activity')}}  {{$t('ScheduleDate')}}  </span></th>
                                                 <th scope="col"><span> {{$t('Activity')}}  {{$t('OWNER')}} </span></th>
                                                 <th scope="col"><span>  {{$t('CreatedAt')}} </span></th>
                                                 <th scope="col"><span>  {{$t('Status')}}  </span></th>
                                                 <th scope="col" class="text-center"><span> {{$t('Action')}}  </span></th>
                                            </tr>
                                        </thead>
                                      <tbody>
                                          <tr v-for="item in followupList">
                                          <td><a href="javascript:;"  v-on:click="openToAddEditFollowUp(item);">{{item.followup_notification_subject}}</a></td>
                                        <td>
                                                    <div v-if="item.activity_type === 'FollowUp'">
                                                         <label>   {{$t('FollowUp')}} </label>
                                                    </div>
                                                       <div v-else>
                                                       {{item.activity_type}}
                                                   </div>
                                                </td>
                                           <td>{{$options.filters.formatDate(item.followup_notification_schedule)}}</td>
                                            <td>{{item.activity_owner}}</td>
                                             <td>{{$options.filters.formatDate(item.created_at)}}</td>
                                             <td><span v-bind:class="{'px-2 bg-success text-white d-block text-center':(item.status_name == 'Active'),'px-2 bg-danger text-white d-block text-center': (item.status_name =='Inactive')}">{{item.status_name}}</span></td>
                                            <td class="text-center single-action"><a href="javascript:;" :title="$t('Delete')" v-on:click="DeleteActivityData(item.followup_notification_id);"><em class="fa fa-trash action-icon text-danger"></em></a></td>
                                       </tr>
                                  </tbody>
                               </table>
                            </div>
                            <div v-if="followupList.length == 0" class="text-center text-danger p-4 border no-record">  {{$t('NoRecordfound')}}</div>
                       </div>
                   </div>
                         <div class="modal-footer">
                              <div class="col-lg-6 text-right p-0">
                                <a href="javascript:;" class="btn btn-success formbtn text-white" v-on:click="openToAddEditFollowUp(null)"><em class="fa fa-plus pr-2"></em>{{$t('Add')}} {{$t('FollowUp')}}</a>
                             </div>
                          </div>
                   </div>
                </div>
            </div>
           <modalevent v-if="showAddEventModalForAddEdit" ref="addEditFollowupModal" @close="showAddEventModalForAddEdit = false"  v-bind:modulename="modulename" v-bind:submodulecode="submodulecode"  :sourceid="sourceId">
            </modalevent>
        </div>
    </transition>
</template>
<script>
import modalevent from '../Modal/modalevent.vue'
import LeadDataService from "../../../services/DataService";
export default {
    components:{
        modalevent
    },
     props: {        
        modulename: {
            type: String,
            required: true
        },
        submodulecode: {
            type: String,
            required: true
        },
        userid: {
            type: Number,
            required: true
        },       
        leadInfo:{
            Type: Object,
            required: true
        },
        srcId:{
            Type: String,
            required: true
        }
    },
    data: function () {
        return {
            followupList: [],
            showAddEventModalForAddEdit: false,
            sourceId: null,
            listItem: null
        }
    },
    created: function () {      
        this.sourceId=this.srcId;
    this.loadFollowUpData(this.leadInfo,this.srcId);
    },
    methods: {
        formatDateTimeFunc: function (value) {
            if (value) {
                return moment(String(value), "YYYY-MM-DD HH:mm").format("MM/dd/yyyy")
            }
        },
        loadFollowUpData: function (listItem, itemId) { 
            if (typeof itemId != "undefined" && itemId != null && itemId != "") {
                 
                var vm = this;
                vm.listItem = listItem;
                vm.sourceId = itemId;               
                vm.followupList = [];
               var params = "id=" + itemId + "&subModuleCode=" + vm.submodulecode + "&moduleName=" + vm.modulename;
               LeadDataService.getFollowupList(params).then(function (response) {
                     
                    if (response.data != "" && response.data != null && response.data.data.length > 0) {
                        vm.followupList = response.data.data.filter(function (itm) { return itm.activity_type == "FollowUp" });
                       setTimeout(function(){vm.CheckBoxBootstrap()}, 100);
                        setTimeout(function(){vm.ResponsiveDataTable('table-openactivities-content')}, 500);
                    } else {
                        vm.followupList = [];
                    }
                });
            }
        },
        DeleteActivityData: function (id) {
            var vm = this;
      if (id > 0) {
                 vm.confirmR(this.$t("ConfirmDelete"),true, this.$t("Confirm"), function ()
                {
                    var postJSON = {
                        ids: id,
                        moduleName: vm.modulename,
                        subModuleCode: vm.submodulecode,
                        refCode: 'DELETE_VIEW_ACTIVITY',
                        refId: vm.sourceId
                    };
                    var objectPostString = JSON.stringify(postJSON);
                     LeadDataService.deleteActivityData(objectPostString)
                     .then(function (response) {
                       if (response.data[0].Status == "Success") {
                       
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('FollowUp')]), "success", true, vm.$t("Alert"));
                            vm.loadFollowUpData(null, vm.sourceId);
                        }
                        else {
                          
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('FollowUp')]), "warning", true, vm.$t("Alert"));
                        }
                         
                    });
                })
            }
        },
        closePopup: function () {
            this.$emit('close');
        },
        openToAddEditFollowUp: function (item) {
            var vm = this;
            if (vm.listItem != null && vm.listItem.is_additional_lead_owner != "NO" && vm.submodulecode == "CRM_LEADS") {
                var json = JSON.parse(vm.listItem.is_additional_lead_owner);
                var prmsn = json.filter(function (c) { return c.permission_code == "AddAcitvities" });
                if (prmsn.length > 0) {
                    vm.showAddEventModalForAddEdit = true;
                    if (item != null) {
                        setTimeout(function () {
                            vm.$refs.addEditFollowupModal.FetchEventViewData(item.followup_notification_id);
                        }, 500);
                    }
                } else {
                    alert(vm.$t("additionalOwnerAuthorityWarning"));
                }
            } else {
                vm.showAddEventModalForAddEdit = true;
                console.log(vm.sourceId);
                if (item != null) {
                    setTimeout(function () {
                        vm.$refs.addEditFollowupModal.FetchEventViewData(item.followup_notification_id);
                    }, 500);
                }
            }
        }
    }
}
</script>